.element {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  height: 30px;
  border: 2px solid #595858;
  border-radius: 5px;
  padding: 2px 5px;
}

@media screen and (min-width: 768px) {
  .element {
    width: 300px;
    height: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .element {
    width: 310px;
    height: 35px;
  }
}

.name {
  font-size: 13px;
  font-weight: 600;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .name {
    font-size: 15px;
  }
}

.tel {
  margin-left: auto;
  font-size: 15px;
}

@media screen and (min-width: 768px) {
  .tel {
    font-size: 17px;
  }
}

.btnDelete {
  background-color: inherit;
  border-width: 0;
  padding: 0;
  margin-top: auto;
}

.icon {
  fill: darkblue;
}
